.mat-code {
  display: inline-block;

  background: rgb(42, 66, 172);
  background: var(--mat-bg);
  color: var(--mat-fg);
  
  border-radius: 5px;
  padding: .025rem .5rem;
  margin: .15rem .1rem;

  &.req-text {
    background-color: rgb(70, 133, 252);
  }

  &.click-target:hover {
    --mat-bg: var(--mat-bg-hover);
  }
}